<template>
  <main class="content container" v-if="productLoading">
    <BaseLoader/>
  </main>
  <main class="content container" v-else-if="productLoadingFailed">   </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{name: 'main'}">
            
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{name: 'main'}">
            {{ category.title }}
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image.file.url" :alt="product.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | numberFormat }} 
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">:</legend>
              <ColorPicker :colors="product.colors"/>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">  :</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">
                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128" checked="">
                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <AmountSelect v-model="productAmount"/>

              <button class="button button--primery" type="submit" :disabled="productAddSending">
                 
              </button>
            </div>

            <div v-if="productAdded">   </div>
            <div v-if="productAddSending">   ...</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
                
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
             GPS, , BEIDOU Galileo  QZSS<br>
              <br>
              Bluetooth Smart, ANT+  Wi-Fi<br>
              <br>
          </p>

          <a href="#">
             
          </a>

          <h3> ?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS   ,     ,  
             . Wahoo ELEMNT BOLT GPS     ANT+,  
            
             .    ,     .    
                 ,      .
               ,      . 
            
                    .
          </p>

          <h3></h3>

          <p>
             Wahoo ELEMNT BOLT  .     74,6 x 47,3 x 22,1
            .
                .      .   
                 56 .      , 
                   : ,  
            ,   .. (     ).     
            
             IPX7.  ,     ,     ( 30 )
                   1 .
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>

import numberFormat from "@/helpers/numberFormat";
import AmountSelect from "@/components/common/AmountSelect";
import ColorPicker from "@/components/common/ColorPicker";
import axios from "axios";
import {API_BASE_URL} from "@/config";
import BaseLoader from "@/components/common/BaseLoader";
import {mapActions} from "vuex";

export default {
  name: 'ProductPage',
  props: ['pageParams'],
  components: {
    AmountSelect,
    ColorPicker,
    BaseLoader
  },
  data: () => ({
    productAmount: 1,
    productData: null,
    productLoading: false,
    productLoadingFailed: false,
    productAdded: false,
    productAddSending: false,
  }),
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    }
  },
  methods: {
    ...mapActions(['addProductToCart']),
    async addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      await this.addProductToCart({productId: this.product.id, amount: this.productAmount});

      this.productAddSending = false;
      this.productAdded = true;
    },
    async loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      try {
        const response = await axios.get(API_BASE_URL + `products/${this.$route.params.id}`);
        this.productData = response.data;
      } catch (error) {
        this.productLoadingFailed = true;
        if (error.response.status === 404)
          this.$router.replace({name: 'notFound'});
      } finally {
        this.productLoading = false;
      }
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true
    }
  },
  filters: {
    numberFormat
  },
}
</script>