<template>
  <BaseLoader v-if="orderInfoLoading" />
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{name: 'main'}">
            
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{name: 'cart'}">
            
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <OrderList :products="orderInfo.basket.items"/>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>{{ orderInfo.basket.items.length }}</b>    <b>{{ orderInfo.totalPrice | numberFormat }} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import OrderList from "@/components/order/OrderList";
import numberFormat from "@/helpers/numberFormat";
import BaseLoader from "@/components/common/BaseLoader";

export default {
  components: {
    OrderList,
    BaseLoader
  },
  data: () => ({
    orderInfoLoading: false
  }),
  methods: {
    async loadOrderInfo() {
      if (this.$store.state.orderInfo?.id === this.$route.params.id) return;

      this.orderInfoLoading = true;
      await this.$store.dispatch('loadOrderInfo', {
        orderId: this.$route.params.id,
        errCallback: () => this.$router.replace({name: 'notFound'})
      });
      this.orderInfoLoading = false;
    }
  },
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo ?? {};
    }
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadOrderInfo();
      },
      immediate: true
    }
  },
  filters: {numberFormat}
}
</script>